body * {
  box-sizing: border-box;
}

h1, h2, h3, h4, p, div, body, a, button, ul, li {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

.container {
  width: 280px;
  margin: 0 auto;
  padding: 0 15px;
}

@media (width >= 768px) and (width <= 1199px) {
  .container {
    width: 700px;
  }
}

@media (width >= 1199px) {
  .container {
    width: 1119px;
  }
}

.header {
  background-color: #ffb8ca;
  display: flex;
}

.header-link {
  color: #fff;
  font-family: DM Sans;
  font-size: 16px;
  font-weight: 700;
  list-style: none;
}

.cow {
  background-color: #fff5f6;
  padding-top: 125px;
  padding-bottom: 139px;
}

.cow-subtitle {
  letter-spacing: 4%;
  text-align: center;
  text-transform: uppercase;
  color: #ffa5ba;
  margin-bottom: 15px;
  font-family: Titan One, sans-serif;
  font-size: 18px;
  font-weight: 400;
}

.cow-title {
  letter-spacing: 6%;
  text-align: center;
  text-transform: uppercase;
  color: #d41443;
  margin-bottom: 26px;
  font-family: Titan One, sans-serif;
  font-size: 34px;
  font-weight: 400;
}

.cow-wrap {
  flex-direction: column;
  display: flex;
}

.cow-cow {
  width: 280px;
  margin-bottom: 40px;
}

.cow-info {
  width: 220px;
}

.cow-text {
  color: #907e82;
  margin-bottom: 19px;
  font-family: DM Sans, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 162%;
}

.cow-text:first-child {
  margin-bottom: 30px;
}

.cow-text:last-child {
  margin-bottom: 17px;
}

.cow-bold {
  color: #000;
}

.cow-button {
  font-family: "DM Sans" sans-serif;
  letter-spacing: 0%;
  cursor: pointer;
  background-color: #fff;
  background-image: url("button-arrow.5ebc0afd.svg");
  background-position: 134px;
  background-repeat: no-repeat;
  border: none;
  border-radius: 22px;
  margin-bottom: 39px;
  padding: 12px 56px 12px 37px;
  font-size: 14px;
  font-weight: 700;
  line-height: 100%;
  display: flex;
}

.cow-list {
  flex-direction: column;
  display: flex;
}

.cow-icon {
  height: 50px;
  margin-bottom: 16px;
}

.cow-number {
  letter-spacing: 4%;
  text-transform: uppercase;
  margin-bottom: 15px;
  font-family: Titan One, sans-serif;
  font-size: 36px;
  font-weight: 400;
}

.cow-fact {
  font-family: DM Sans, sans-serif;
  font-size: 12px;
  font-weight: 700;
  line-height: 170%;
}
